<template>
	<myp-list-cell class="row">
		<view v-if="getImgsLength===1" style="flex-direction:row;height: 100%;width: 100%;">
			<myp-icon :name="item.imgs[0]" :iconStyle="imgStyle"></myp-icon>
			<view style="flex: 1;justify-content: space-between;margin-left: 8rpx;">
				<myp-title class="row-title" :boxStyle="titleBoxStyle" :title1="item.title" :title1Style="title1Style">
				</myp-title>
				<myp-title class="row-remark" :boxStyle="titleBoxStyle" :title1="getRemark"
					:title1Style="title1RemarkStyle" :leftIcon1="item.icon" :title3="item.top!=0?'置顶':''"
					title3Style="font-size: 12px;" title3Type="error"></myp-title>
			</view>
		</view>
		<view v-else-if="getImgsLength>1" style="width: 100%;">
			<myp-title class="row-title" :boxStyle="titleBoxStyle" :title1="item.title" :title1Style="title1Style">
			</myp-title>
			<view
				style="flex-direction: row;justify-content: flex-start;margin-bottom: 8rpx;overflow: hidden;height: 100%;width: 100%;">
				<myp-icon v-for="(img,i) in item.imgs" :key="i" :name="img" boxStyle="width:33.33%;padding-right:1.5%"
					:iconStyle="imgsStyle" ></myp-icon>
			</view>
			<myp-title class="row-remark" :boxStyle="titleBoxStyle" :title1="getRemark" :title1Style="title1RemarkStyle"
				:leftIcon1="item.icon" :title3="item.top!=0?'置顶':''" title3Style="font-size: 12px;" title3Type="error">
			</myp-title>
		</view>
		<template v-else >
			<myp-title class="row-title" :boxStyle="titleBoxStyle" :title1="item.title" :title1Style="title1Style">
			</myp-title>
			<myp-title class="row-remark" :boxStyle="titleBoxStyle" :title1="getRemark" :title1Style="title1RemarkStyle"
				:leftIcon1="item.icon" :title3="item.top!=0?'置顶':''" title3Style="font-size: 12px;" title3Type="error">
			</myp-title>
		</template>
	</myp-list-cell>
</template>

<script>
	export default {
		components: {},
		props: ["item"],
		data: function() {
			return {
				titleBoxStyle: "height:auto;",
				title1Style: `font-size: 18px;margin-bottom: 8rpx;`,
				title1RemarkStyle: "color:#999696;font-size: 12px;",
				imgStyle: "height:80px;width:100px;",
				imgsStyle: "height:80px;width:100%;",
			}
		},
		computed: {
			getRemark() {
				return `${this.item.ref} 评论(${ this.item.commentNum }) ${ this.item.time }`
			},
			getImgsLength() {
				return this.item.imgs?.length || 0
			}
		},
		watch: {},
		created: function() {},
		mounted: function() {},
		methods: {},
	}
</script>

<style lang="scss" scoped>
	.row {
		display: flex;
		width: 100vw;
		// margin-bottom: 20rpx;
		padding: 16rpx;
		background-color: white;
		// box-shadow: 0px 0px 2px rgb(238, 238, 238);
		border-bottom: 1px solid rgb(238, 238, 238);

		/deep/ .myp-icon-box {
			height: 100%;

			.myp-image,
			& div {
				background-size: 100% 100% !important;
			}
		}

		/deep/ .row-title {
			.myp-flex-row {
				width: 100%;

				.myp-size- {
					width: 100%;

					span {
						display: -webkit-box;
						-webkit-box-orient: vertical;
						-webkit-line-clamp: 2;
						width: 100%;
						text-overflow: ellipsis;
						overflow: hidden;
					}
				}
			}
		}
	}
</style>
